import uniList from "../../components/uni-list/uni-list.vue"
import uniListItem from "../../components/uni-list-item/uni-list-item.vue"
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			data:'',
			pic_1:'',
			st_id:0,
			subscribetime:'请选择时间',
			selectUser:false,
			stname:'请选择技师',
			address:'请选择地址',
			name:''
		}
		},
	onLoad(options) {
		this.stname = options.stname;
		this.st_id = options.st_id;
		this.pic_1 = options.pic_1;
		this.pr_id = options.pr_id;
		this.subscribetime = options.subscribetime;
		let pr_id = options.pr_id;
		uni.request({
			url:"https://dongyi.sir6.cn/api/project/"+pr_id,
			method:'GET',
			success: (res) => {
				this.data = res.data;
			}
		});
		var t = this;
		setInterval(function(){
			uni.request({
				url: t.apiServer+'/api/address/index?user_id='+uni.getStorageSync('USERID'),
				method: 'GET',
				success: res => {
					var data = res.data;

					if(data.err==1){
						uni.showModal({
							title:'错误提示',
							content:'请完善您的服务地址',
							showCancel:false
							
						});
					}else{
						t.address = data.data.address;
						t.name = data.data.name;
						t.mobile = data.data.mobile;
						t.longitude = data.data.longitude;
						t.latitude = data.data.latitude;
					}
				},
			});
		},2500)
	},
	onShow() {
		this.myaddress();
	},
	methods: {
		subscribe:function(){
			if(this.st_id==undefined){
				uni.showModal({
					title:'错误提示',
					content: '请选择技师，以及预约时间',
					showCancel: false
				});
			}else{
				uni.request({
					url:"https://dongyi.sir6.cn/api/order/save",
					data:{
						st_id:this.st_id,
						pr_id:this.pr_id,
						subtime:this.subscribetime,
						address: this.address,
						address_contacts:this.name,
						address_mobile:this.mobile,
						longitude:this.longitude,
						latitude:this.latitude,
						user_id:uni.getStorageSync('USERID')
						
					},
					method:'POST',
					success: (res) => {
						console.log(res);
						uni.showModal({
							title:'信息提示',
							content:res.data.msg,
							showCancel:false
						});
						if(res.data.err==0){
							uni.redirectTo({
								url: '/pages/myself/orders?status=0'
							});
						}
					}
				})
			}
		},
		myaddress(){//获取当前用户地址
			uni.request({
				url: this.apiServer+'/api/address/index?user_id='+uni.getStorageSync('USERID'),
				method: 'GET',
				success: res => {
					var data = res.data;
					if(data.err==1){
						uni.showModal({
							title:'错误提示',
							content:'请完善您的服务地址',
							showCancel:false
						});
					}else{
						this.address = data.data.address;
						this.name = data.data.name;
						this.mobile = data.data.mobile;
						this.longitude = data.data.longitude;
						this.latitude = data.data.latitude;
					}
				},
			});
		},
	}
}
